.note-popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  


  .popup-content {
    background: white;
    border-radius: 10px;
    padding: 20px;
    width: 350px; 
    position: relative;
  }
  


  .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 20px;
    cursor: pointer;
  }
  


  .popup-title,
  .popup-tagline,
  .popup-body {
    width: 90%;
    max-width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  


  .popup-body {
    height: 150px; 
    resize: vertical; 
  }
  


  .popup-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  


  .save-btn, .delete-btn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  


  .save-btn {
    background-color: #28a745;
    color: white;
  }
  


  .save-btn:hover {
    background-color: #218838;
  }
  


  .delete-btn {
    background-color: #dc3545;
    color: white;
  }
  

  
  .delete-btn:hover {
    background-color: #c82333;
  }
  
  